@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@500;600;800&display=swap');

:root{
  /* https://codepen.io/sdthornton/pen/wBZdXq */
  --shadow0: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.12);
  --shadow1: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
  --shadow2: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  --shadow3: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  --shadow4: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  --shadow5: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
}

*{
  font-family: 'Raleway', sans-serif;
}

body{
  background-color: white;
  margin:0;
}

.Tag{
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  padding:6px 10px;
  margin:5px;
  border-radius: 15px;
  cursor: pointer;
  display: inline-block;
  background-color: lightcoral;
  color:white;
}


.Tag[highlight=true]{
  background-color:white;
  color:black;
}


.Tag:hover{
  box-shadow: var(--shadow2);
  position: relative;
  z-index: 10;
  background-color: white;
  color:black
}


.Repos{
  text-align: left;
  //border-left: lightcoral 3px solid;
  padding-left: 10px;
}

.Repos > h1{
  margin:0;
}

.Repos > h1 > span{
  background-color:lightcoral;
  color:white;
  padding:3px;
  border-radius: 5px;
}


[shown=false]{
  display: none !important;
}

.repo{
  background-color: white;
  display: block;
  margin: 10px 0 10px 0;
  text-align: left;
  padding:10px;
  cursor: pointer;
  width: 100%;
  
}


.repoWrapper{
  width: 100%;

}

.repoWrapper > a{
  width: 100%;
  text-decoration: none;
  color:black;
  display: block;
}


.repo > div > *{
  margin:5px;
}

.tagWrapper{
  width: 100%;
  padding:20px 20px;
  margin: 20px 0;
  text-align: left;
  background-color: lightcoral;
  border-radius: 5px;
}



.Intro{
  background-color:rgba(0,0,0,.05);
  background-color: #EEE;
  min-height:50vh;
  text-align: left;
  display: flex;
  flex-flow: column nowrap;
  position: relative;
  padding:50px;
}

.Intro > div{
  margin: 100px auto;
  max-width: 700px;
}

.Intro > div> h1{
  margin: 0;
  font-size: 150px;
  display: inline-block;
}

.Intro > div > h3{
  margin:0;
}







.content{
  max-width: 700px;
}



.Pages{
  margin: 0;
}

.Tabs{
  background-color: #EEE;
  //position: sticky;
  top:0;
}

.Tabs > span{
  margin:auto;
  display: flex;
  justify-content: flex-start;
}

.Tabs > span > button{
  font-size: 15px;
  padding:10px;
  border-radius: 5px 5px 0 0;
  box-shadow: none;
  border:none;
  margin: 0 5px;
  cursor:pointer;
  background:none;
}

.Tabs > span > button[highlight=true]{
  background-color: white;
}

.Tabs > span > button[highlight=false]:hover{
  background-color: rgba(0,0,0,0.05);
  background-color: rgba(255,255,255,.6);
}

button:focus {outline:0;}


.Page{
  margin: 100px auto;
  max-width: 700px;
  min-height:100vh;
}


.Contact{
  text-align: left;
}

.Contact > a, .Contact > p{
  text-decoration: none;
  display: inline-block;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);

  font-size: 20px;
  padding: 5px 10px;
  border-radius: 5px;
  color:black;
  margin: 5px 0;
}

.Contact > p{
  display: block;
  font-size: 25px;
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 5px;

  margin: 5px 0;
}

.Contact > a:hover{
  color:lightcoral;
}

.Resume, .Vcard{
  background-color:lightcoral;
  color:white !important;
  font-weight: bold;
  box-shadow: var(--shadow1);
}

.Resume:hover, .Vcard:hover{
  box-shadow: var(--shadow2);
  position: relative;
  z-index: 10;
}

.CallToAction{
  text-align: left;
  margin-bottom:50px;
}

.CallToAction > h1 > span{
  background-color:lightcoral;
  color:white;
  padding:3px;
  border-radius: 5px;
}

.CallToAction > a{
  background-color: black;
  background-color: lightcoral;
  color:white;
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  padding:6px 10px;
  margin:5px;
  border-radius: 15px;
  cursor: pointer;
}

